const FarmDemosQuery = gql`
  query {
    farmDemos(pagination: { limit: 100 }, sort: "createdAt:desc") {
      data {
        id
        attributes {
          Farmer
          FarmLocationLatitude
          FarmLocationLongitude
          lgas {
            data {
              id
              attributes {
                Name
              }
            }
          }
          crop {
            data {
              id
              attributes {
                Name
              }
            }
          }
          AreaOfField
          isPesticidesUsed
          FarmerPractise {
            id
            Yield
            DateOfSowing
            DateOfDemonstration
            FirstUreaApplication
            SecondUreaApplication
          }
          IndoramaPractise {
            id
            Yield
            DateOfSowing
            DateOfDemonstration
            FirstUreaApplication
            SecondUreaApplication
          }
          Images {
            data {
              attributes {
                url
              }
            }
          }
          DateOfHarvesting
          Season
          Status
          state {
            data {
              id
              attributes {
                Name
              }
            }
          }
        }
      }
    }
  }
`;
